@import url("./variables.css");

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html, body {
    height: 100%;
    font-family: "Inter";
    font-weight: 600;
    font-size: 20px;
    line-height: 1.3;
    background-color: var(--main-color);
    color: var(--main-color);
    scroll-behavior: smooth;
    overflow-x: hidden;
}

a {
    color: var(--main-color);
    text-decoration: none;
}